.footer{margin:1.5rem 0;font-size:.6rem;text-align:center;width:100%;color:#666}.footer>a{color:#666;margin-right:.2rem}.footer.position{position:absolute;bottom:1rem;margin:0}.footer .gh-btn{position:relative;background-color:#eee;background-image:-webkit-gradient(linear, left top, left bottom, color-stop(0, #fcfcfc), color-stop(100%, #eee));background-image:-webkit-linear-gradient(top, #fcfcfc 0, #eee 100%);background-image:linear-gradient(to bottom, #fcfcfc 0, #eee 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#fcfcfc', endColorstr='#eeeeee', GradientType=0);background-repeat:no-repeat;border:1px solid #d5d5d5;padding:2px 5px 2px 4px;color:#333;text-decoration:none;text-shadow:0 1px 0 #fff;white-space:nowrap;cursor:pointer;border-radius:.15rem}.footer .gh-btn:active{background-image:none;background-color:#dcdcdc;border-color:#b5b5b5;box-shadow:inset 0 2px 4px rgba(0,0,0,0.15)}.footer .gh-btn .gh-ico{display:block;position:absolute;width:.7rem;height:.7rem;margin-right:.2rem;background-image:url(data:image/svg+xml;base64,);background-size:100% 100%;background-repeat:no-repeat;left:.1rem;top:.05rem}.footer .gh-btn .gh-text{padding-left:.75rem}


